import React from 'react';
import { useHistory } from 'react-router';

const Login = () => {
    var history=useHistory();
    return (
        <div>
            
             <div className="container mt-5">
                <div className="row justify-content-center">
                <div className="col-md-7">
                          <h2 className="ml-5 res">Login page</h2>
            </div>
                    <div className="col-md-7">
           <form>
           {/* <div className="form-group">
                    <label htmlFor="">Enter NAME:</label>
                    <input type="text" className="form-control"  name="name" />
            </div>
                <div className="form-group">
                    <label htmlFor="">password :</label>
                    <input type="password" className="form-control"  name="price" />
                </div> */}

                                    <div className="form-group">
                                    <label htmlFor="">Enter NAME:</label>
                                    <input type="text" className="form-control"  name="name" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="">password :</label>
                                    <input type="text" className="form-control" name="password" />
                                </div>

                <div class="form-group"> 
                     <button className="btn btn-dark col-md-3 mr-3" onClick={(e)=>{
                           e.preventDefault();
                         history.push("/home")
                     }}>login</button>
                     <button className="btn btn-danger col-md-3 ml-3" onClick={(e)=>{
                           e.preventDefault();
                         history.push("/registor")
                     }}>Registor</button>
                </div>
           </form>
        </div>
        </div>
        </div>
        </div>
    );
};


const Reg = () => {
    var history=useHistory();
    return (
        <div>
            <div className="container mt-5">
                <div className="row justify-content-center">
                     <div className="col-md-7">
                          <h2 className="ml-5 res">Register</h2>
                     </div>
                    <div className="col-md-7">
                    <form>
                        <div className="form-group">
                              <label htmlFor="">Name :</label>
                              <input type="text" name="name" className="form-control"/>
                        </div>
                       
                        <div className="form-group">
                              <label htmlFor="">password :</label>
                              <input type="password" name="password" className="form-control"/>
                        </div>
                        <div className="form-group">
                              <label htmlFor="">confirm password :</label>
                              <input type="password" name="confirmpassword" className="form-control"/>
                        </div>

                        <div className="form-group">
                              <label htmlFor="">Email :</label>
                              <input type="email" name="email" className="form-control"/>
                        </div>

                <div class="form-group"> 
                     <button className="btn btn-danger col-md-3" onClick={(e)=>{
                        e.preventDefault();
                        history.push("/login");
                     }}>Registor</button>
                     <button className="btn btn-dark col-md-3 ml-4" onClick={(e)=>{
                         e.preventDefault();
                         history.push("/login")
                     }}>login</button>
                </div>
           </form>
         </div>
    </div>
    </div>
           
        </div>
    );
};

export {Login,Reg};