import React from 'react';

const Purches_display = (props) => {
    var item=props.myitem;
    return (
              <div className="col-md-4 my-2">
                <div class="card">
                <img class="card-img-top" src={`/imgs/${item.img}`} alt="Card image cap" />
                <div class="card-body">
                    <h5 class="card-title">{item.name}</h5>
                    <h3>₹{item.price}</h3>
                          <a href={`/purchaseitem/${item.id}`} class="btn btn-outline-light bg-danger deleteitem">Delete</a>
                </div>
                </div>
                </div>
     
    );
};

export default Purches_display;