import React from 'react';
import Purches_display from '../display_components/Purches_display';



const Pur = (props) => {
    var data=props.data;
    var s=props.s;
    console.log(s);
    return (
        <div>
              <div className="container mt-3">
                  <div className="row justify-content-center">
                            {
                            data.map((i)=>
                            {
                              return  <Purches_display myitem={i}/>
                            })
                            }
                  </div>
              </div>
        </div>
    );
};

export default Pur;