import React from 'react';
import { useHistory } from 'react-router';

const Purches_display = (props) => {
    var item=props.myitem;
    var history=useHistory();
    return (
              <div className="col-md-4 my-2">
                <div class="card">
                <img class="card-img-top" src={`/imgs/${item.img}`} alt="Card image cap" />
                {/* <img class="card-img-top" src={`https://source.unsplash.com/1600x900/?${item.name}`} alt="Card image cap" /> */}
                <div class="card-body">
                    <h5 class="card-title">{item.name}</h5>
                    <h3>₹{item.price}</h3>
                    <p className="lead">{item.name} Lorem, ipsum dolor sit amet consectetur adipisicing.</p>
                    <h2>Avaliable - {item.Quantity}</h2>
                    {/* <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p> */}
                    <button class="btn btn-outline-warning bg-dark purchesbtn mr-2" onClick={()=>{
                        history.push(`/purchaseitem/${item.id}`);
                    }}>Purchase Now</button>
                </div>
                </div>
                </div>
     
    );
};

export default Purches_display;