import React, { useState } from 'react';
import { useHistory, useParams } from 'react-router';
// import Button from '@mui/material/Button';
const MYpur = (props) => {
     var {set_Ac_price,Ac_price,totalPriceFun}=props;
    var para=useParams();
    var history=useHistory();
    var  data=props. data[para.id];
    var [myprice,setprice]=useState(0);
    var q="";
    var updateprice=(e)=>{
        q=e.target.value;
        setprice(q*data.price);
    }
    var myitembuy=()=>{
            var myitemdata={
                Quantity:myprice/data.price,
                price:data.price,
               name:data.name
            }
            totalPriceFun(myitemdata);
            history.push("/purchaseitem");
    }
    var buyitem=(e)=>{
        e.preventDefault();
        var myitemdata1={
            Quantity:myprice/data.price,
            price:data.price,
           name:data.name
        }
        totalPriceFun(myitemdata1);
        history.push("/buy");
    }
    return (
        <div>
            <div className="container" >
                   <div className="row justify-content-center">
                       <div className="col-md-6">
                       <form  className="mt-5" autoComplete="off">
                                <div className="form-group">
                                    <label htmlFor="">ITEM NAME:</label>
                                    <input type="text" className="form-control" value={data.name} name="name" readOnly />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="">PRICE :</label>
                                    <input type="number" className="form-control" value={data.price} name="price" readOnly />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="">QUANTITY :</label>
                                    <input type="number" className="form-control" min="1" max={data.Quantity}   name="qun" onChange={updateprice} />
                                </div>
                                <div className="form-group mt-">
                                     <button className="btn btn-danger ml-4 px-3 col-5" type="button" onClick={buyitem}>BUY</button>
                                     <button className="btn btn-dark ml-4 px-5 col-5" type="reset">CANCLE</button>
                                     <button className="btn btn-outline-warning bg-dark ml-4 px-3 col-5 mt-3" type="button" onClick={myitembuy}>+</button>
                                     {/* <button className="btn btn-dark ml-4 px-5 col-5 mt-3" type="reset">CANCLE</button> */}
                                </div>
                        </form> 
                       </div>
                       <div className="col-md-6 " id="MYPUR">
                             <h1> AMOUNT : ₹  {myprice}</h1>
                       </div>
                   </div>
            </div>
        </div>
    );
};

export default MYpur;