import React from 'react';
import Deleteitem from '../display_components/Deleteitem';

const Pur = (props) => {
    var data=props.data;
    return (
        <div>
              <div className="container mt-3">
                  <div className="row justify-content-center">
                            {
                            data.map((i)=>
                            {
                              return  <Deleteitem myitem={i}/>
                            })
                            }
                  </div>
              </div>
        </div>
    );
};

export default Pur;