import React from 'react';

const Buy = ({s}) => {
    console.log(s);
    var mytotalamt=0;
    var tag=document.getElementById("PURCHASEITEM");
   tag.removeAttribute("href");
    console.log(tag);
    return (
        <>
            <div className="container">
                   <div className="row justify-content-center mt-5">
                        <div className="col-md-7">
                               <table className="table table-bordered table-hover w-100">
                                    <thead className="table-dark">
                                         <tr>
                                              <th>Product Name</th>
                                              <th>Price</th>
                                              <th>Quantity</th>
                                              <th>AMOUNT</th>
                                         </tr>
                                    </thead>
                                    <tbody>
                                         {
                                             s.map((i)=>{
                                                mytotalamt=(i.price)*(i.Quantity)+mytotalamt
                                                 return(
                                                      <Myrow data={i}/>
                                                 )
                                             })
                                         }
                                    </tbody>
                                    <tfoot>
                                        <tr className="bg-dark text-light text-center">
                                            <th colSpan="3">TOTAL AMOUNT</th>
                                            <th >{mytotalamt}</th>
                                        </tr>
                                    </tfoot>
                                </table>     
                         </div>   
                    </div>    
            </div>   
        </>
    );
};

export default Buy;



var Myrow=(props)=>{
    var data=props.data;
     return (
         <>
            <tr className="tr">
                <th>{data.name}</th>
                <th>{data.price}</th>
                <th>{data.Quantity}</th>
                <th>{data.price*data.Quantity}</th>
            </tr>
         </>
     )
}