import React, { useState } from 'react';
import { useHistory } from 'react-router';
const Add = () => {
    var history=useHistory();
    var [MYdata,setdata]=useState({
            name:"",price:"",qun:""
    });
    var onchangedata=(e)=>{
        var name=e.target.name;
        var value=e.target.value;
        setdata({...MYdata,[name]:value})
        console.log(MYdata); 
    }

    var addMYitem=async (e)=>{
        e.preventDefault();
          { console.log(MYdata);}
          const {name,price,qun}=MYdata;
          try{
          var res=await fetch("/student",{
              method:"POST",
              headers:{
                  "Content-Type":"application/json"
              },
              body:JSON.stringify({
                  name,price,qun
              })
          })
          const d=await res.json();
        console.log(d);
        }catch(e){
            window.alert("error");
            {console.log(e)}
        }
    }

    return (
        <div>
            <div className="container">
                   <div className="row justify-content-center">
                       <div className="col-6">
                       <form  className="mt-5" autoComplete="off" method="POST">
                                <div className="form-group">
                                    <label htmlFor="">ITEM NAME:</label>
                                    <input type="text" className="form-control" value={MYdata.name} name="name" onChange={onchangedata}/>
                                </div>
                                <div className="form-group">
                                    <label htmlFor="">PRICE :</label>
                                    <input type="number" className="form-control" value={MYdata.price} name="price"  onChange={onchangedata}/>
                                </div>
                                <div className="form-group">
                                    <label htmlFor="">QUANTITY :</label>
                                    <input type="number" className="form-control"  value={MYdata.qun} name="qun"  onChange={onchangedata}/>
                                </div>
                                <div className="form-group mt-">
                                     <button className="btn btn-success ml-4 px-3" type="button" onClick={addMYitem}>ADD ITEM</button>
                                     <button className="btn btn-warning ml-4 px-5" type="reset" value="cancle">RESET</button>
                                    <button className="btn btn-danger ml-4 px-5" onClick={()=>{
                                        history.push("/home");
                                    }}>HOME </button>
                                </div>
                        </form> 
                       </div>
                   </div>
            </div>
        </div>
    );
};

export default Add;